#AboutBox {
  position: absolute;
  display: flex;
  border-radius: 7px;
  top: 85px;
  width: 1000px;
  padding-left: 6px;
  background-color: #0e0e0e91;
  backdrop-filter: blur(6px);
}

.container {
  position: relative;
  width: 800px;
  height: 488px;
}

.red {
  color: #db6262;
  padding-left: 1px;
}

#centerMe {
  display: flex;
  justify-content: center;
}

/* -_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_ */
/* ---Buttons--- */
/* Button for CV */
/* ------------- */
#ButtonCV {
  border: none;
  border-radius: 10px;
  color: white;
  background-color: #313bac;
  margin-left: 8px;
  cursor: pointer;
  font-size: 17px;
  font-weight: 600;
  text-decoration: none;
  padding: 4px 10px 8px;
  position: absolute;
  bottom: 18px;
}
#ButtonCV:hover {background-color: #1a2060;}
#svgCV {
  position: relative;
  filter: invert(100%);
  top: 5px;
  left: 5px;
  width: 23px;
}
/* ----------------- */
/* Button for Github */
/* ----------------- */
#ButtonG {
  border: none;
  border-radius: 15px;
  color: white;
  background-color: #313bac;
  margin-left: 15px;
