/* -_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_ */
/* ----Font styles---- */
/*    Tittle Text      */
/* ------------------- */
#TittleText {
  color: white;
  margin-top: 10px;
  margin-bottom: 2px;
  margin-left: 10px;
  font-size: 33.2px;
  font-weight: 600;
}
/* ------------------- */
/*    Position Text    */
/* ------------------- */
#Position {
  color: white;
  font-size: 22px;
  margin-top: 6px;
  margin-left: 12px;
  position: relative;
  bottom: 63px;
  left: 80px;
  text-decoration: underline;
  text-underline-offset: 6px;
  text-decoration-thickness: 6px;
  text-decoration-color: #6d4848;
}
/* ------------------- */
/*      Bio Text       */
/* ------------------- */
#BioTittle {
  font-size: 1.17em;
  font-weight: bold;
  color: white;
  position: relative;
  margin-left: 8px;
  bottom: 37px;
  text-decoration: underline;
  text-underline-offset: 6px;
  text-decoration-thickness: 4px;
  text-decoration-color: #6d4848;
}
#BioText {
  color: rgb(207, 207, 207);
  font-size: 15px;
  font-weight: 400;
  position: relative;
  margin-left: 8px;
  margin-top: 23px;
  bottom: 47px;
  width: 480px;
  margin-right: 6px;
  text-align: justify;
  text-indent: 15px;
}
#Bio {
  position: relative;
  top: 3px;
}
/* ------------------- */
/*   Certificate Text  */
/* ------------------- */
#WebCertificate {
  color: white;
  font-weight: 500;
  margin-left: 8px;
  position: relative;
  bottom: 22px;
}
#WebDevelopment {
  color: #db6262;
  text-decoration: none;
}
#WebDevelopment:hover {
  color: #db6262;
  text-decoration: underline;
}
/* -_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_-_ */
/* -------Icons------- */
/*   Certificate Text  */
/* ------------------- */
#DoggoIcon {
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  margin-left: 5px;
}
